export default {
    onPullDownRefresh(){
         this.getdata()
    },
    data() {
        return {
            mode: false,
            userData: [],
            URL: getApp().globalData.URL,
        };
    },
    created() {
        this.getdata()
    },
    onNavigationBarButtonTap(e) {
        if (e.index === 0) {
            this.to('sign')
        }
    },
    methods: {
        changeMode() {
            this.mode = !this.mode;
        },
       async getdata() {
            await this.$u.get('/get_user').then(res => {
                if (res.errorCode !== 0) return;
                this.userData = res.data[0];
                uni.stopPullDownRefresh();
            })
            
        },
        to(page,type) {
            if(type){
              uni.navigateTo({
                  url: `../${page}/${page}?type=${type}`,
                  
              })
            }else{
                uni.navigateTo({
                    url: `../${page}/${page}`
                })
            }
           
        },
        login() {
            uni.navigateTo({
                url: "../login/login"
            })
        },
        register() {
            uni.navigateTo({
                url: "../register/register"
            })
        },
        outin() {
            uni.showModal({
                title: '提示',
                content: '确认退出登录吗',
                success: (res)=>{
                    if (res.confirm) {
                        uni.clearStorage();
                        uni.reLaunch({
                            url: "../login/login"
                        })
                    }
                }
            });

        }
    }
}
